      flex-direction: column;
  margin: 8px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  background-color: #FFF;
}

.Card:hover {
  cursor: pointer;
}

.Selected {
  border: 1px solid #E25950;
  -webkit-box-shadow: 2px 4px 6px #525F7F;
          box-shadow: 2px 4px 6px #525F7F
}



